.icon {
  background-image: url($spritesheet-image);
  background-repeat: no-repeat;
	width: 36px;
	height: 36px;
}

.icon__accomodations {
  // @include sprite($icon-accomodations);
  background-position: 0px 0px;
  background-size: 270%;
}

.icon__food {
  background-position: 61% 0;
  background-size: 270%;
  // @include sprite($icon-food);
}

.icon__presents {
  background-position: 3px 66%;
  background-size: 276%;
  height: 39px;
  margin-top: -0.3rem;
  // @include sprite($icon-presents);
}

.icon_wrapper {
  margin-left: 1.4rem;
}

