/*-------------------------------------*\
      Teaser
      teaser.css
\*-------------------------------------*/

.teaser {
  background-image: url(../img/teaser__shape.png);
  background-position: center bottom;
  background-repeat: no-repeat;
  margin-top: 2.3rem;
  padding-bottom: 2.4rem;
  text-align: center;
  z-index: 1;

  &__header {
    font-size: 1.1rem;
    line-height: 1.6rem;
    padding-left: 1.6rem;
    padding-right: 1.6rem;
  }

  &__text {
    display: block;
    font-size: 0.9rem;
    line-height: 1.8rem;
    padding-left: 1rem;
    padding-right: 1rem;
    padding-top: 1.8rem;
    text-align: center;
  }
}
